export default "@import url('https://fonts.googleapis.com/css2?family=Hind+Madurai:wght@300;400&family=Rajdhani:wght@600;700&display=swap');\r\n\r\n/* font-family: 'Hind Madurai', sans-serif;\r\nfont-family: 'Rajdhani', sans-serif;    \r\n#199900 */\r\nbody {\r\n  font-family:      'Hind Madurai', sans-serif;\r\n  font-weight:      400;\r\n  font-size:        1.5rem;\r\n  background-color: #efefef;\r\n  padding:          0 0 0 20rem;\r\n}\r\n\r\nbody>nav {\r\n  position:       fixed;\r\n  box-sizing:     border-box;\r\n  left:           0;\r\n  top:            0;\r\n  width:          20rem;\r\n  display:        flex;\r\n  flex-direction: column;\r\n  padding:        2rem 1.5rem;\r\n}\r\n\r\n.code-tabs {\r\n  border:        1px solid rgb(240, 240, 240);\r\n  border-radius: 3px;\r\n}\r\n\r\n.code-tabs pre,\r\n.code-tabs input {\r\n  display: none;\r\n}\r\n.code-tabs input:checked+pre {\r\n  display: block;\r\n}\r\n\r\n.code-tabs label {\r\n  color: rgba(191, 191, 191, 1);\r\n}\r\n\r\n.code-tabs input:checked+label {\r\n  color: rgba(38, 38, 38, 1);\r\n}\r\n\r\n.code-tabs ul {\r\n  font-size:     0;\r\n  padding:       0;\r\n  white-space:   nowrap;\r\n  overflow:      auto;\r\n  user-select:   none;\r\n  border-bottom: 1px solid rgb(240, 240, 240);\r\n}\r\n\r\n.code-tabs li {\r\n  list-style: none;\r\n  display:    inline-block;\r\n  position:   relative;\r\n}\r\n\r\n.code-tabs label {\r\n  cursor:      pointer;\r\n  user-select: none;\r\n  display:     inline-block;\r\n  padding:     2px 5px;\r\n  margin:      5px;\r\n  font-size:   14px;\r\n}\r\n\r\n.code-tabs li~li::before {\r\n  content:    \"\";\r\n  height:     12px;\r\n  width:      1px;\r\n  background: rgba(191, 191, 191, 1);\r\n  position:   absolute;\r\n  top:        50%;\r\n  transform:  translate(-50%, -50%);\r\n}";